.cloud1{
    width:200px;
    position:absolute; 
    bottom:0px; 
    left:0px; 
}
.cloud2{
    width:200px;
    position:absolute; 
    top:-5px; 
    right:0px;
    /* z-index: -11;  */
}
@media only screen and (max-width:600px) {
    .cloud1 , .cloud2{
        width: 100px;
    }
}