.container{
    height: 100vh;
    /* margin-top: 70px; */
    box-sizing: border-box;
    font-family: cursive;
    background-color: white;
    border: 2px solid transparent;

}
.link{
    display: inline;
    color: blue;

}
.h4{
    text-align: center;
    margin-top: 100px;
    color: black;
}
.note{
    text-align: center;
    font-size: 12px;
    margin: 10px 0 0 0 ;
    color: gray;
}
@media only screen and (max-width:700px) {
    .h4{
        /* width: 80%; */
        font-size: 12px;
        margin-top: 40px;
    }
    
}