button{
    display: block;
    position: relative;
    width: 100%;
    padding: 0;
    margin: auto;
    margin-top: 30px;
    box-sizing: border-box;
    height: 45px;
    border: 1px solid grey;
    border-radius: 10px;
    background-color: #0927d3;
    
    /* box-shadow: 0px 0px 8px 1px #888888;  */
    transition: all .2s ease-in-out;
    color: white;
}
button:active{
    position: relative;
    top: 1px;
}
button:hover{
 box-shadow:0px 0px 8px 1px #888888;
}
